header{
    width: 100%;
    height: 100px;

    .inner{
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 1500px;
        height: 100%;
        margin: auto;

        nav{
            ul{
                display: flex;
                align-items: center;
                justify-content: flex-start;
                gap: 24px;

                a{
                    font-size: 20px;
                }
            }
        }
    }
}